
import Vue from 'vue'
import Router from 'vue-router'
import store from "../store/index"

Vue.use(Router)

const login = () => import('../pages/login/login')
const index = () => import('../pages/index/index')
// const menuAdd = () => import('../pages/menu/menuAdd')

const role = () => import('../pages/role/role')
const manger = () => import('../pages/manger/manger')
const menu = () => import('../pages/menu/menu')
const spces = () => import('../pages/spces/spces')
const loop = () => import('../pages/loop/loop')
// const seck = () => import('../pages/seck/seck')
const seck = () => import('../pages/seck/seck')
const classify = () => import('../pages/classify/classify')
const goods = () => import('../pages/goods/goods')
const member = () => import('../pages/member/member')

//判断是否有权限进入相应的组件
function isRoute(path,next){
  let userInfo = store.state.userInfo
  if(userInfo.menus_url.includes(path)){
      next()
  }else{
    next('/index')
  }
}

export const indexRouters = [
  {
    path: "home",
    component: () => import('../pages/home/home'),
  },
  {
    path: "role",
    name: "角色管理",
    component: role,
    beforeEnter(to,from,next){
      isRoute('/role',next)
    }
  },
  {
    path: "manger",
    name: "管理员管理",
    component: manger,
    beforeEnter(to,from,next){
      isRoute('/manger',next)
    }
  },
  {
    path: "loop",
    name: "轮播图管理",
    component: loop,
    beforeEnter(to,from,next){
      isRoute('/loop',next)
    }
  },
  {
    path: "menu",
    component: menu,
    name: "菜单管理",
    beforeEnter(to,from,next){
      isRoute('/menu',next)
    }
  },
  {
    path: "spces",
    name: "商品规格",
    component: spces,
    beforeEnter(to,from,next){
      isRoute('/spces',next)
    }
  },
  {
    path: "seck",
    name: "秒杀活动",
    component: seck,
    beforeEnter(to,from,next){
      isRoute('/seck',next)
    }
  },
  {
    path: "classify",
    name: "商品分类",
    component: classify,
    beforeEnter(to,from,next){
      isRoute('/classify',next)
    }
  },
  {
    path: 'goods',
    name: "商品管理",
    component: goods,
    beforeEnter(to,from,next){
      isRoute('/goods',next)
    }
  },
  {
    path: 'member',
    name: "会员管理",
    component: member,
    beforeEnter(to,from,next){
      isRoute('/member',next)
    }
  }
]

let router = new Router({
  routes: [
    {
      path: "/login",
      component: login
    },
    {
      path: "/index",
      component: index,
      children: [
        {
          path: '',
          redirect: 'home'
        },
        
      ].concat(indexRouters)
    }
  ]
})
//登陆拦截
router.beforeEach((to, from, next) => {
  if (to.path === '/login') {
    next();
    return;
  }
  let userInfo = store.state.userInfo;
  if(userInfo.username){
      next();
      return;
  }
  next('/login')
})



export default router



